@import url(https://fonts.googleapis.com/css?family=Raleway:300);
@import url(https://cdn.galenguyer.com/fontawesome/css/solid.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  text-align: center;
  background-color: #2e3141;
  color: #f5f5f5;
  font-family: Raleway, Helvetica, sans-serif;
}

.songlist {
  max-width: 640px;
  margin: auto;
  padding-bottom: 32px;
  display: table;
}

#currentSongisPlaying {
  font-size: 1.3rem;
  font-style: bold;
  margin-bottom: 16px;
}
#currentSongisPaused {
  font-size: 1.3rem;
  font-style: bold;
  margin-bottom: 16px;
}
#currentSong>hr {
    max-width: 85%;
    background-color:white;
    margin: 12px auto;
}
#currentSongisPlaying::before {
  font-family: "Font Awesome 5 Free";
  content: "\f04b";
  margin-right: 12px;
}
#currentSongisPaused::before {
  font-family: "Font Awesome 5 Free";
  content: "\f04c";
  margin-right: 12px;
  font-size: inherit;
}

.note {
  font-style: italic;
  color: #bdbdbe;
}
.note>a{
  color: #f5f5f5;
}
.song {
    display: inline-block;
    padding: 0px;
    margin: 0px 8px 0px 8px;
    font-size: 1.05rem;
    color: #f5f5f5;
}

.artists {
    font-style: italic;
    color: #bdbdbe;
}

a {
    text-decoration: none;
    color: #f5f5f5
}

hr {
    max-width: 75%;
    background-color:white;
    border: solid 1.2px;
    margin: 14px auto 14px auto;
}
